<script>
export default{
  data(){
    return{
    }
  },
    mounted(){

      this.open();
      
    },
  
    methods:{
   
      open(){
        let tabs=document.querySelectorAll('.box');
        for(let i=0;i<tabs.length;i++){
            tabs[i].onclick=function(){
               for(let i=0;i<tabs.length;i++){
                  tabs[i].classList.remove('active')
               }
               this.classList.add('active')
            }
        }
      }
        
      },
 
     


